body {
  background-color: #282c34;
}

.App {
  text-align: center;
  max-width: 728px;
  margin: 0 auto;
  position:relative;
}

.App header {
  background-color: #f7f7f7;
  color:black;
  height: 10vh;
  min-height: 50px;
  position: fixed;
  width: 100%;
  max-width: 728px;
  top: 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
  z-index: 99;
  padding: 10px;
  box-sizing: border-box;
  border-bottom: 5px solid rgb(240, 5, 5);
}

.App section {
  display: flex;
  flex-direction: column;
  justify-content: center;
  min-height: 100vh;
  background-color: rgb(247, 247, 247);
}

main {
  padding: 10px;
  height: 80vh;
  margin: 10vh 0 10vh;
  overflow-y: scroll;
  display: flex;
  flex-direction: column;
}

main::-webkit-scrollbar {
  width: 0.25rem;
}

main::-webkit-scrollbar-track {
  background: #1e1e24;
}

main::-webkit-scrollbar-thumb {
  background: #6649b8;
}

form {
  height: 10vh;
  position: fixed;
  bottom: 0;
  background-color: rgb(24, 23, 23);
  width: 100%;
  max-width: 728px;
  display: flex;
  font-size: 1.5rem;
}

form button {
  width: 20%;
  background-color: rgb(56, 56, 143);
}


input {
  line-height: 1.5;
  width: 100%;
  font-size: 1.5rem;
  background: rgb(58, 58, 58);
  color: white;
  outline: none;
  border: none;
  padding: 0 10px;
}

button {
  background-color: #282c34; /* Green */
  border: none;
  color: white;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  cursor: pointer;
  font-size: 1.25rem;
}

button:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}


.sign-in {
  color: #ffffff;
  background: rgb(12, 3, 254);
  max-width: 400px;
  margin: 0 auto;
}
.sign-in-no {
  color: #ffffff;
  background: rgb(254, 0, 228);
  max-width: 400px;
  margin: 0 auto;
}
.warning{
  color:black;
  text-align: center;
  max-width:100%;
  margin:0;
}




ul, li {
  text-align: left;
  list-style: none;
}

p {
  max-width: 500px;
  margin-bottom: 12px;
  line-height: 24px;
  padding: 10px 20px;
  border-radius: 10px;
  position: relative;
  color: white;
  text-align: center;
}

.message {
  display: flex;
  align-items: center;
  font-weight: 500;
}


.sent {
  flex-direction: row-reverse;
}

.sent p {
  color: rgb(5, 5, 5);
  align-self: flex-end;
  border:1px solid #0b93f6;
  text-align: right;
}
.received p {
  background: #e5e5ea;
  color: black;
  text-align: left;
}
.owner p{
  color:white;

   background-image: linear-gradient(to right top, #d16ba5, #c777b9, #ba83ca, #aa8fd8, #9a9ae1, #8aa7ec, #79b3f4, #69bff8, #52cffe, #41dfff, #46eefa, #5ffbf1);
}

img {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  margin: 2px 5px;
}

.alternate-form-submit{
  color:black;
  position:absolute;
  bottom:1rem;
  color:red;
  font-size:20px;
  background-color: white;
  outline:1px solid pink;
  border-radius: 10px;
  left:50%;
  width:90%;
  transform: translate(-50%,0);
  padding:10px;
}